@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');

.topcontent{
    color: white;
    position: absolute;
}
.topcontent__container{
    position: absolute;
    top: 300px;
    right: 150px;

}
.topcontent__container > p{
    letter-spacing: 1px;
    text-align: left;
    padding: left 3px; ;
    padding-top: 20px;
}
.topcontent__container > a > button
{
    margin-bottom: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;
}
.topcontent__container  > h1
{
    font-size: 50px;
    font-family: "Montserrat",sans-serif;

}